/* Reset
/* ---------------------------------------------------------- */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    /* 
        baseline: align element baseline with parent baseline
        middle: align element middle with parent middle plus 
            half of parent's x-height
    */
    vertical-align: baseline;
}
body {
    /* set height of a line box */
    line-height: 1;
}
ol,
ul {
    /* the style of the dot b/f each item in a list */
    list-style: none;
}
blockquote,
q {
    /* style of a quote (q or blockquote) */
    quotes: none;
}
/* remove any text inserted before / after the quote block by the browser */
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    /* spacing b/t cell, `border-spacing <length>` */
    border-spacing: 0;
    /* set cell inside table to share borders */
    border-collapse: collapse;
}
img {
    /* set img to block-element (vertically stacked, with padding/border/margin) */
    display: block;
    /* set max-width to 100% of its parent element */
    max-width: 100%;
    /* set heigth according to width */
    height: auto;
}
html {
    /* `width` is content box width. but if box-sizing is set to border-box, it set
        width of the border area */
    box-sizing: border-box;
    font-family: sans-serif;

    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
a {
    /* background includes content, padding, border, but not margin */
    background-color: transparent;
}
a:active,
a:hover {
    /* Unlike border, outline doesn't change the size of the element */
    /* Also, outline is drawn outside border, overlapping with margin  */
    outline: 0;
}
b,
strong {
    font-weight: bold;
}
i,
/* em: emphasis element */
em,
/* dfn: definition element */
dfn {
    font-style: italic;
}
h1 {
    /* 1 em equals to the current font size (by default, font size is 16px) */
    margin: 0.67em 0;
    font-size: 2em;
}
small {
    /* set to 80% of parent font-size */
    font-size: 80%;
}
/* subscript and superscript */
sub,
sup {
    /* 
    `position` sets how an element is positioned in a document. 
        The top, right, bottom, and left properties determine 
        the final location of positioned elements. 
    
    static: positioned according to the normal flow. top, right,
        bottom, left, and z-index have no effect.
    
    relative: positioned according to the normal flow, and then 
        offset relative to itself based on the values of top, right,
        bottom, and left. 
        
        The offset does not affect the position of any other elements;
        thus, the space given for the element in the page layout is the
        same as if position were static.
    
        create stacking context and z-index is applied
    
    absolute: removed from normal flow, no space is created from the element,
        positioned relative to its closest positioned containing block. 
        (an element is positioned if position is anything but static)
        (compute position relative to the content box of that block)

        create stacking context and z-index is applied

    */
    position: relative;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}
img {
    border: 0;
}
/* target all svg element except for the root svg element */
svg:not(:root) {
    overflow: hidden;
}
/* highlight */
mark {
    background-color: #fdffb6;
}
code,   
kbd,    /* keyboard input */
pre,    /* preformatted text */
samp {  /* sample output from a computer program */
    font-family: monospace, monospace;
    font-size: 1em;
}
kbd {
    padding: 3px 5px;
    font-family: var(--font-mono);
    font-size: 1.5rem;
    background: #f6f8fa;
    border: 1px solid rgba(124, 139, 154, 0.25);
    /* make round-corner border */
    border-radius: 6px;
    box-shadow: inset 0 -1px 0 rgba(124, 139, 154, 0.25);
}
@media (max-width: 600px) {
    kbd {
        font-size: 1.3rem;
    }
}
button,
input,
optgroup,
select,
textarea {
    margin: 0; /* 3 */
    color: inherit; /* 1 */
    font: inherit; /* 2 */
}
button {
    overflow: visible;
    border: none;
}
button,
select {
    text-transform: none;
}
button,
/* input element with type="button" */
html input[type="button"],
/* 1 */
input[type="reset"],
input[type="submit"] {
    /* change cursor to a pointer when hovering over element,
        indicating it's clickable */
    cursor: pointer; /* 3 */

    -webkit-appearance: button; /* 2 */
}
button[disabled],
html input[disabled] {
    cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}
input {
    line-height: normal;
}
input:focus {
    outline: none;
}
input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}
input[type="search"] {
    box-sizing: content-box; /* 2 */

    -webkit-appearance: textfield; /* 1 */
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
legend {
    padding: 0; /* 2 */
    border: 0; /* 1 */
}
textarea {
    overflow: auto;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
td,     /* table data cell */
th {    /* table header cell */
    padding: 0;
}

/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
    font-size: 62.5%;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
    color: var(--color-darkgrey);
    font-family: var(--font-sans);      /* Use font-sans as default*/
    font-size: 1.6rem;
    line-height: 1.6em;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 0;
    text-rendering: optimizeLegibility;
    background: #fff;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: "liga" on;
}

::selection {
    text-shadow: none;
    background: #daf2fd;
}

/* thematic break between paragraphs */
hr {
    position: relative;
    display: block;
    width: 100%;
    margin: 2.5em 0 3.5em;
    padding: 0;
    height: 1px;
    border: 0;
    border-top: 1px solid #f0f0f0;
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

textarea {
    resize: vertical;
}

::not(.gh-content) p,
::not(.gh-content) ul,
::not(.gh-content) ol,
::not(.gh-content) dl,
::not(.gh-content) blockquote {
    margin: 0 0 1.5em 0;
}

ol,
ul {
    padding-left: 1.3em;
    padding-right: 1.5em;
}

ol ol,
ul ul,
ul ol,
ol ul {
    margin: 0.5em 0 1em;
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
}

ul,
ol {
    max-width: 100%;
}

li {
    padding-left: 0.3em;
    line-height: 1.6em;
    margin-top: 0.5em;
}

/* li + li {
    margin-top: 0.5em;
} */

dt {
    float: left;
    margin: 0 20px 0 0;
    width: 120px;
    color: #daf2fd;
    font-weight: 500;
    text-align: right;
}

dd {
    margin: 0 0 5px 0;
    text-align: left;
}

blockquote {
    margin: 1.5em 0;
    padding: 0 1.6em 0 1.6em;
    border-left: #daf2fd;
}

blockquote small {
    display: inline-block;
    margin: 0.8em 0 0.8em 1.5em;
    font-size: 0.9em;
    opacity: 0.8;
}
/* Quotation marks */
blockquote small:before {
    content: "\2014 \00A0";
}

blockquote cite {
    font-weight: bold;
}
blockquote cite a {
    font-weight: normal;
}

a {
    color: #15171A;
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    line-height: 1.15;
    font-weight: 600;
    text-rendering: optimizeLegibility;
    letter-spacing: -0.01em;
}

h1 {
    margin: 0 0 0.5em 0;
    font-size: 4.8rem;
    font-weight: 700;
    letter-spacing: -0.015em;
}
@media (max-width: 600px) {
    h1 {
        font-size: 2.8rem;
    }
}

h2 {
    margin: 1.5em 0 0.5em 0;
    font-size: 2.8rem;
    font-weight: 700;
}
@media (max-width: 600px) {
    h2 {
        font-size: 2.3rem;
    }
}

h3 {
    margin: 1.5em 0 0.5em 0;
    font-size: 2.4rem;
    font-weight: 600;
}
@media (max-width: 600px) {
    h3 {
        font-size: 1.7rem;
    }
}

h4 {
    margin: 1.5em 0 0.5em 0;
    font-size: 2rem;
}
@media (max-width: 600px) {
    h4 {
        font-size: 1.7rem;
    }
}

h5 {
    margin: 1.5em 0 0.5em 0;
    font-size: 2rem;
}

h6 {
    margin: 1.5em 0 0.5em 0;
    font-size: 1.8rem;
}
